import React from "react"
import '../scss/main.scss';
import Sidebar from '../components/Sidebar';
import SidebarButton from '../components/SidebarButton';

const logoStyle = {
  width: '300px',
  border: '0',
  boxShadow: 'none',
  marginBottom: '0'
}

export default () => (
  <div className="content">
    <SidebarButton />
    <Sidebar />
    <div className="container">
      <div className="row">
        <div className="col-12">
          <div className="center u-m-top--lg u-bg--white u-p-left--lg u-p-right--lg u-p-top--lg u-p-bottom--lg u-m-bottom--lg">
            <img alt="DisputeLab" src="./img/form-state.svg" style={logoStyle} width="80px"/>
            <h1>Oops! That page doesn't exist</h1>
            <p>Try a different page using the navigation</p>
          </div>
        </div>
      </div>
    </div>
  </div>
)
